import Vue from 'vue'
import App from './App'
import VueMinRichEditor from 'vue-minrich-editor'

Vue.config.productionTip = false
Vue.use(VueMinRichEditor)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App, VueMinRichEditor }
})



