<template>
  <div id="app">
    <h1 class="title">Minimalistic rich editor for Vue</h1>
    <div class="col">
      <rich-editor placeholder="..." v-model="html"></rich-editor>
    </div>
    <div v-html="html" class="col">

    </div>
  </div>
</template>

<script>
  export default {
    components: {},
    data () {
      return {
        html: ''
      }
    },
    name: 'app'
  }
</script>

<style>
  #app {
    display:     flex;
    flex-wrap:   wrap;
    width:       100%;

    font-family: 'Merriweather', serif;
    font-size: 16px;
  }

  h1, h2, h3 {
    width:  100%;
    margin: 0;
  }

  h1.title {
    border-bottom: 2px solid black;
    margin-bottom: 1rem;
  }

  blockquote {
    font-size: 2em;
    color: gray;
    margin: 0 1em;
  }

  p {
    margin: 0 0 1em;
  }

  div.col {
    width: 50%;
    padding-right: 1rem;
    box-sizing: border-box;
  }
</style>



